.catalog {
    background-color: #92A6B0;
    color: #FFF;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    padding: 20px;
}

.catalogItem {
    transition:all 0.2s ease-in-out;
    cursor: pointer;
    &:hover {
        background-color: darken($color: #92A6B0, $amount: 5);
    }
}